<template>
  <div>
    <v-toolbar app class="header-nav-component elevation-1">
      <v-container fluid class="py-2">
        <v-layout row justify-space-between>
          <v-flex xs3 md2>
            <router-link :to="{ name: 'home' }">
              <v-img class="logo-image" contain :src="require('../../assets/images/logo.svg')"></v-img>
            </router-link>
          </v-flex>
          <v-flex>
            <v-layout justify-end align-center fill-height>
              <router-link :to="{ name: 'cart'}" class="icon-link">
                <v-badge left overlap color="#2196f3" >
                  <span v-if="cartItemsCount && cartItemsCount > 0"
                        slot="badge">{{cartItemsCount}}</span>
                  <v-icon >fas fa-shopping-cart</v-icon>
                </v-badge>
              </router-link>
            </v-layout>
          </v-flex>
        </v-layout>
      </v-container>

      <v-container fluid class="subheader py-0"
                   v-if="hasNameButton || hasDesignButton || hasProductButton">
        <v-layout row justify-space-between>
          <v-flex>
            <v-toolbar-items>
              <v-btn
                v-if="hasNameButton"
                class="subheader-button"
                flat
                @click="namesDialogIsOpen = true"
              >Change name</v-btn>
              <v-btn
                v-if="hasDesignButton"
                class="subheader-button"
                flat
                @click="designsDialogIsOpen = true"
              >Change design</v-btn>
              <router-link :to="{name: 'products'}" class="subheader-link">
                <v-btn
                  v-if="hasProductButton"
                  class="subheader-button"
                  flat
                >Change product</v-btn>
              </router-link>
            </v-toolbar-items>
          </v-flex>
        </v-layout>
      </v-container>
    </v-toolbar>
    <ModalDialog :dialogIsOpen="designsDialogIsOpen" @closeDialog="designsDialogIsOpen = false">
      <DialogDesign @closeDialog="designsDialogIsOpen = false" />
    </ModalDialog>
    <ModalDialog :dialogIsOpen="namesDialogIsOpen" @closeDialog="namesDialogIsOpen = false">
      <DialogName @closeDialog="namesDialogIsOpen = false" />
    </ModalDialog>
  </div>
</template>

<script>
import DialogDesign from '../DialogDesign/DialogDesign';
import DialogName from '../DialogName/DialogName';
import ModalDialog from '../Layout/ModalDialog';

export default {
  name: 'HeaderNav',
  data() {
    return {
      designsDialogIsOpen: false,
      namesDialogIsOpen: false
    }
  },
  watch: {
    '$route.params': 'closeModals'
  },
  components: { ModalDialog, DialogDesign, DialogName },
  computed: {
    cartItemsCount() {
      return this.$store.state.cart.items && this.$store.state.cart.items.length;
    },
    hasDesignButton() {
      const pages = ['product', 'products', 'productCategory'];
      return pages.indexOf(this.$route.name) !== -1;
    },
    hasNameButton() {
      const pages = ['product', 'products', 'productCategory'];
      return pages.indexOf(this.$route.name) !== -1;
    },
    hasProductButton() {
      const pages = ['product'];
      return pages.indexOf(this.$route.name) !== -1;
    }
  },
  methods: {
    closeModals() {
      this.designsDialogIsOpen = false;
      this.namesDialogIsOpen = false;
    }
  }
}
</script>

<style lang="scss">
  .header-nav-component {
    height: auto;
    >.v-toolbar__content {
      height: auto !important;
      flex-wrap: wrap;
      padding: 0;
    }

    .icon-link {
      text-decoration: none;
      .v-badge__badge {
        top: -10px;
        left: -19px;
        width: 19px;
        height: 19px;
        @media screen and (max-width: 600px) {
          font-size: 13px;
          top: -7px;
          left: -16px;
          width: 16px;
          height: 16px;
        }
      }
    }
    .logo-image {
      max-height: 35px;
      min-width: 90px;
    }

    .subheader {
      background: #ffffff;
    }
    .subheader-button {
      height: 30px !important;
    }
    .subheader-link {
      text-decoration: none;
    }

    @media screen and (max-width: 600px) {
      .subheader-button {
        font-size: 11px;
        padding: 0 5px;
      }
    }
  }
</style>
