import Vue from 'vue';
import Vuex from 'vuex';
import Backend from './services/backend';
import createLogger from 'vuex/dist/logger';

const backend = new Backend();

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    namesList: {
      list: [],
      isLoading: false,
      error: null,
      current: null,
      // currentSlug: null
    },
    designCategories: {
      list: [],
      isLoading: false,
      error: null,
      current: null,
      currentSubcategory: null
    },
    designs: {
      list: [],
      isLoading: false,
      error: null,
      current: null,
      designSubcategorySlug: null,
      noImages: {}
    },
    productCategories: {
      list: [],
      isLoading: false,
      error: null,
      current: null,
    },
    products: {
      list: [],
      isLoading: false,
      error: null,
      current: null,
      productSubcategorySlug: null
    },
    featuredProducts:{
      list:[],
      isLoading: false,
      error: null,
      current: null
    },
    orders: {
      list: [],
      isLoading: false,
      error: null,
      current: null,
    },
    cart: loadCartFromLocalStorage()
  },
  mutations: {
    setName(state, value) {
      // if name selected from list
      if (value && typeof value === 'object') {
        state.namesList.current = value;
      } // if name is custom
      else {
        state.namesList.current = {
          name: value,
          slug: null
        }
      }
    },
    setNameSlug(state, slug) {
      state.namesList.current = state.namesList.list.find((item) => item.slug === slug) || { name: slug, slug: null };
    },

    // DESIGN CATEGORIES
    setDesignCategory(state, value) {
      state.designCategories.current = value;
    },
    setDesignSubcategory(state, value) {
      state.designCategories.currentSubcategory = value;
    },

    // DESIGNS
    setCurrentDesign(state, slug) {
      state.designs.current = state.designs.list.find((item) => item.slug === slug);
    },
    setNoDesignImage(state, { nameSlug, designSlug } ) {
      let noImages = Object.assign({}, state.designs.noImages);
      if (noImages[nameSlug]) {
        noImages[nameSlug].push(designSlug);
      } else {
        noImages[nameSlug] = [designSlug];
      }
      state.designs.noImages = noImages;
    },

    //PRODUCTS
    setCurrentProduct(state, slug) {
      state.products.current = state.products.list.find((item) => item.slug === slug);
    },

    //CART
    // item: {name, design, product, productColor, productSize, count}
    addCartItem(state, item) {
      let cartItems = [].concat(state.cart.items);
      cartItems.push(item);
      state.cart.items = cartItems;
    },
    setItemsToCart(state, items) {
      state.cart.items = items;
    },

    // FETCH
    fetchStart(state, { key, slug}) {
      state[key].isLoading = true;
      if (key === 'designs') {
        state.designs.designSubcategorySlug = slug;
      }
      if (key === 'products') {
        state.products.productSubcategorySlug = slug;
      }

    },
    fetchSuccess(state, { key, data, slug }) {
      state[key].isLoading = false;
      state[key].error = null;
      state[key].list = data;
    },
    fetchError(state, { key, error }) {
      state[key].isLoading = false;
      state[key].error = error;
      if (key === 'designs') {
        state.designs.designSubcategorySlug = null;
      }
      if (key === 'products') {
        state.products.productSubcategorySlug = null;
      }
    },


  },
  actions: {
    addCartItem({ commit, state }, item) {
      commit('addCartItem', item);
      saveCartToLocalStorage(state.cart);
    },
    deleteCartItem({ commit, state }, item) {
      let filteredItems = state.cart.items.filter(value => value !== item);
      commit('setItemsToCart', filteredItems);
      saveCartToLocalStorage(state.cart);
    },
    setCartItemCount({ commit, state }, { item, count} ) {
      let cart = Object.assign({}, state.cart);
      let filteredItems = cart.items.filter(value => value === item);
      if (filteredItems && filteredItems.length) {
        filteredItems[0].count = count;
      }
      commit('setItemsToCart', cart.items);
      saveCartToLocalStorage(state.cart);
    },
    clearCart({ commit, state }) {
      commit('setItemsToCart', []);
      saveCartToLocalStorage(state.cart);
    },
    fetch({ commit, state }, { key, slug }) {
      return new Promise((resolve, reject) => {
        if (state[key].isLoading) return;
        let keyCapitalized = key.replace(/^\w/g, l => l.toUpperCase());
        commit(`fetchStart`, { key, slug });
        backend[`get${keyCapitalized}`](slug)
          .then(res => {
            commit(`fetchSuccess`, { key, slug, data: res.data });
            resolve({ data: res.data });
          })
          .catch(error => {
            commit(`fetchError`, { key, error: handleErrorMessage(error) });
            reject({ error: handleErrorMessage(error) });
          })
      })
    },

    fetchIfEmpty({ commit, state, dispatch }, { key, slug }) {
      if (key === 'designs' && state.designs.designSubcategorySlug === slug) return;
      if (key === 'products' && state.products.productSubcategorySlug === slug) return;
      if (!state[key].list || !state[key].list.length || key === 'designs' || key === 'products') {
        return dispatch('fetch', { key, slug })
      }
    }
  },

  getters: {
    designSubcategories: state => {
      let currentCategory = state.designCategories.current && state.designCategories.current.slug;
      if (!currentCategory) return [];
      return state.designCategories.list.find((item) => item.slug === currentCategory).subcategories;
    },
    featuredProductsList: state => {
      let list = state.featuredProducts.list.map((item) => {
        item.product.images = [item.image];
        item.design.lightness = item.design.image.lightness;
        item.design.image = item.design.image.url;
        delete item.image;
        return item;
      });
      return list;
    }
  },
  //plugins: [createLogger()]
});

function handleErrorMessage(error) {
  return error.response && error.response.data &&
    JSON.stringify(error.response.data).replace(/[[\]{}]+/g, '') ||
    'Something went wrong. Please try again later';
}

function loadCartFromLocalStorage() {
  let cart;
  const emptyCart = {
    items: []
  };
  try {
    cart = JSON.parse(localStorage.getItem('cart'))
  } catch(e) {
    return emptyCart
  }
  if (!cart || !cart.items) return emptyCart;
  return cart;
}

function saveCartToLocalStorage(cart) {
  localStorage.setItem('cart', JSON.stringify(cart));

}

