<template>
  <v-container class="featured-products-component">
    <h3 class="text-uppercase headline">Featured Products</h3>
    <v-layout v-if="isLoading" justify-center row class="mt-4">
      <Preloader size="80"/>
    </v-layout>
    <v-layout mt-4 row fill-height wrap >
      <v-layout v-if="isLoading" justify-center row class="mt-4">
        <Preloader size="80"/>
      </v-layout>
      <v-flex v-else xs12 sm6 md4 lg3 v-for="item in featuredProducts" class="card">
        <v-card
          class="design-card"
          tile
          flat
          :to="{
            name: 'product',
            params: Object.assign({}, $route.params, {
              name: name,
              subcategory: item.design.category.slug,
              design: item.design.slug,
              productCategory: item.product.category.slug,
              product: item.product.slug
              })
          }"
        >
          <v-layout justify-center>
            <v-flex xs10>
              <ProductWithDesign
                :design="item.design"
                :product="item.product"
                :name="name"
              />
            </v-flex>
          </v-layout>
          <span class="headline">{{ item.product.name }}</span>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import ProductWithDesign from "../components/ProductWithDesign";
import Preloader from "../components/Layout/Preloader";

export default {
  name: 'FeaturedProductsPage',
  components: { ProductWithDesign, Preloader },
  computed: {
    name() {
      return this.$route.params.name;
    },
    isLoading() {
      return this.$store.state.featuredProducts.isLoading;
    },
    featuredProducts() {
      return this.$store.getters.featuredProductsList;
    }
  },
  created() {
    this.loadData();
  },

  methods: {
    loadData() {
      this.$store.dispatch('fetchIfEmpty', { key: 'featuredProducts' });
    }
  }
}
</script>

