<template>
  <div class="home-component">
    <v-container fluid fill-height>
      <v-layout column justify-center>
        <h3 class="display-2 white--text font-weight-bold mb-5">
          Personalization Made Simple.
        </h3>
        <p>
          <NameInput @submit="dialogForWhoIsOpen = true"/>
        </p>
      </v-layout>
    </v-container>
    <ModalDialog
      :dialogIsOpen="dialogForWhoIsOpen"
      @closeDialog="dialogForWhoIsOpen = false"
      :persistent="true"
    >
      <DialogForWho @closeDialog="onDialogForWhoClose"/>
    </ModalDialog>
    <ModalDialog
      :dialogIsOpen="dialogDesignIsOpen"
      @closeDialog="dialogDesignIsOpen = false"
      :persistent="true"
    >
      <DialogDesign @closeDialog="onDialogDesignClose"/>
    </ModalDialog>
  </div>
</template>

<script>
import NameInput from '../components/NameInput.vue';
import DialogForWho from '../components/DialogForWho/DialogForWho.vue';
import DialogDesign from '../components/DialogDesign/DialogDesign.vue';
import ModalDialog from '../components/Layout/ModalDialog';
import Backend from '../services/backend';

const backend = new Backend();

export default {
  name: 'HomePage',
  components: {
    NameInput,
    DialogForWho,
    ModalDialog,
    DialogDesign
  },
  data() {
    return {
      dialogForWhoIsOpen: false,
      dialogDesignIsOpen: false,
    }
  },
  created() {
    // check if auto
    backend.auth()
      .then(() => {
        this.$store.dispatch('fetchIfEmpty', { key: 'namesList' });
        this.$store.dispatch('fetchIfEmpty', { key: 'designCategories' });
      });
  },
  methods: {
    onDialogForWhoClose(event) {
      this.dialogForWhoIsOpen = false;
      if (event && event.openNext) {
        this.dialogDesignIsOpen = true;
      }
    },
    onDialogDesignClose() {
      this.dialogDesignIsOpen = false;
    }
  }
};
</script>

<style lang="scss">
  .home-component {
    background-image: url('../assets/images/slider.png');
    height: 100%;
    min-height: 70vh;
  }
</style>
