<template>
  <v-dialog
    v-model="isOpen"
    :persistent="persistent"
  >
    <v-card class="card modal-dialog">
      <v-layout justify-end>
        <v-icon
          x-large
          @click="$emit('closeDialog')"
          class="font-weight-black close-icon"
        >
          close
        </v-icon>
      </v-layout>
      <slot></slot>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: 'ModalDialog',
  props: [ 'dialogIsOpen', 'persistent' ],
  data() {
    return {
      isOpen: false
    }
  },
  watch: {
    dialogIsOpen(newVal) {
        this.isOpen = newVal;
    },
    isOpen(newVal) {
        if (!newVal) {
          this.$emit('closeDialog');
        }
    }
  }
}
</script>

<style lang="scss">
.modal-dialog {
  min-height: 300px;

  .close-icon {
    &:hover {
      color: rgba(0, 0, 0, 0.85)
    }
  }
}
</style>
