<template>
  <v-container grid-list-xl class="dialog-name-component">
    <v-layout column>
      <h3 class="text-uppercase headline">Change The Name</h3>
      <v-layout mt-4 row fill-height wrap >
        <v-flex >
          <NameInput @submit="onSubmit"/>
        </v-flex>
      </v-layout>
    </v-layout>
  </v-container>
</template>

<script>
import Backend from '../../services/backend';
import NameInput from '../NameInput';

const backend = new Backend();

export default {
  name: 'DialogName',
  components: {
    NameInput
  },
  computed: {

  },
  created() {

  },
  methods: {
    onSubmit(name) {
      this.$router.push({ name: this.$route.name, params: {
        ...this.$route.params,
        name: name.slug || name
      }});
      this.$emit('closeDialog');
    },
    onChangeStep(step) {
      this.currentStep = step;
    },
  }
};
</script>

