<template>
  <v-container class="designs-component" >
    <v-layout column justify-start>
      <v-layout row :wrap="$vuetify.breakpoint.smAndDown" fill-height align-top>
        <v-flex>
          <v-btn flat  @click="goBack" absolute class="mt-2 hidden-sm-and-down headline">
            <v-icon class="mr-1" >fas fa-chevron-left</v-icon> Back
          </v-btn>
          <v-btn flat block @click="goBack" class="mt-2 hidden-md-and-up headline">
            <v-icon class="mr-1">fat fa-chevron-left</v-icon> Back
          </v-btn>
        </v-flex>
        <v-flex xs12>
          <h3 class="text-uppercase display-2 mb-4">Designs</h3>
        </v-flex>
      </v-layout>
      <v-container grid-list-xl>
        <v-layout mt-4 row fill-height wrap >
          <v-layout v-if="isLoading" justify-center row class="mt-4">
            <Preloader size="80"/>
          </v-layout>
          <v-flex v-else xs12 sm6 md4 lg3 v-for="item in designs" class="card">
            <v-card class="design-card" tile flat @click="selectDesign(item)">
              <v-layout justify-center>
                <v-flex xs10 class="design-wrapper" :style="{'background': item.background_color}">
                  <v-img
                    class="design-image"
                    :src="item.activeImage"
                    @error="onImageLoadingError(item)"
                  ></v-img>
                </v-flex>
              </v-layout>
              <span class="headline">{{ item.name }}</span>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-layout>
  </v-container>
</template>

<script>
  import Preloader from "./Layout/Preloader";
  export default {
    name: 'Designs',
    props: [ 'nextRouteName' ],
    components: {Preloader},
    created() {
      this.fetchDesigns();
      this.fetchNamesList();
    },
    computed: {
      name() {
        return this.$store.state.namesList.current
          && (this.$store.state.namesList.current.slug || this.$store.state.namesList.current.name) ;
      },
      isLoading() {
        return this.$store.state.designs.isLoading;
      },
      designSubcategorySlug() {
        return this.$store.state.designCategories.currentSubcategory &&
          this.$store.state.designCategories.currentSubcategory.slug;
      },
      designs() {
        if (this.$store.state.namesList.isLoading) return [];
        let noImages = this.$store.state.designs.noImages[this.name] || [];
        let namesList = this.$store.state.namesList.list;
        let designsList = this.$store.state.designs.list;
        if (!namesList.find(item => item.slug === this.name)) {
          return designsList.map(item => Object.assign(item, { activeImage: item.image }));
        }
        // replace logo default variant with demo, if it wasn't loaded
        return designsList.map((item) => {
          if (noImages.indexOf(item.slug) !== -1) {
            item.activeImage = item.image;
          } else {
            item.activeImage = item.image.replace(/name(.[\w]+$)/, this.name + '$1');
          }
          return item;
        });
      }
    },
    methods: {
      fetchDesigns() {
        let slug = this.designSubcategorySlug;
        this.$store.dispatch('fetchIfEmpty', { key: 'designs', slug });
      },
      fetchNamesList() {
        this.$store.dispatch('fetchIfEmpty', { key: 'namesList', currentSlug: this.$route.params.name });
      },
      goBack() {
        this.$emit('submit', 'chooseDesignSubcategory');
      },
      selectDesign(item) {
        if (this.$route.name === 'home') {
          this.$router.push({ name: 'products', params: {
            name: this.name,
            subcategory: this.designSubcategorySlug,
            design: item.slug
          }});
        } else {
          this.$router.push({ name: this.$route.name, params: {
            ...this.$route.params,
              subcategory: this.designSubcategorySlug,
            design: item.slug
          }});
        }

        // if (!this.nextRouteName) {
          this.$emit('closeDialog');
        // }
      },
      onImageLoadingError(item) {
        this.$store.commit('setNoDesignImage', { nameSlug: this.name, designSlug: item.slug });
      }
    }
  } ;
</script>
<style lang="scss">
  .designs-component {
    min-height: 500px;
    .design-card {
      cursor: pointer;
    }
    .design-image {
      max-width: 300px;
      margin-left: auto;
      margin-right: auto;
      min-height: 100px;
    }
    .design-wrapper {
      border-radius: 5px;
    }
  }
</style>
