import axios from 'axios';

let instance = null;

class Backend {
  API_URL = process.env.VUE_APP_API_URL;
  authAxios = null;
  accessToken = null;
  refreshToken = null;
  ACCESS_KEY = '5ca61a5b24260d0c2c37dc23538063e6fbc28c0e';
  SECRET_KEY = 'bbf0ca9aeed8c27ef423fffb62e2524dedf19445';
  PARTNER = 'disney';
  APP = 'b7047056-e93d-48a9-83e6-5c2b42b099ed';

  constructor() {
    if (!instance) {
      instance = this;
      let storageAccessToken = localStorage.getItem('accessToken');
      let storageRefreshToken = localStorage.getItem('refreshToken');
      if (storageAccessToken) {
        this.accessToken = storageAccessToken;
        this.refreshToken = storageRefreshToken;
        this.setAuthAxios(this.accessToken);
      }
    }
    return instance;
  }

  auth() {
    let data = {
      access_key: this.ACCESS_KEY,
      secret_key: this.SECRET_KEY
    };
    return new Promise((resolve, reject) => {
      axios.post(this.API_URL + 'auth/login/app/', data)
        .then((response) => {
          this.accessToken = response.data.access_token;
          this.refreshToken = response.data.refresh_token;
          this.setAuthAxios(response.data.access_token);
          localStorage.setItem('accessToken', this.accessToken);
          localStorage.setItem('refreshToken', this.refreshToken);
          resolve(response);
        })
        .catch(error => {
          console.log(error);
          reject(error);
        });
    });
  }

  refresh() {
    let data = {
      refresh_token: this.refreshToken
    };
    return new Promise((resolve, reject) => {
      axios.post(this.API_URL + 'auth/refresh/', data)
        .then((response) => {
          this.accessToken = response.data.access_token;
          this.refreshToken = response.data.refresh_token;
          this.setAuthAxios(response.data.access_token);
          localStorage.setItem('accessToken', this.accessToken);
          localStorage.setItem('refreshToken', this.refreshToken);
          resolve(response);
        })
        .catch(error => {
          console.log(error);
          reject(error);
        });
    });
  }

  getNamesList() {
    return this.getRequest(`partners/${this.PARTNER}/names/`);
  }
  getDesignCategories() {
    return this.getRequest(`partners/${this.PARTNER}/design-categories/`);
  }
  getDesigns(slug) {
    return this.getRequest(`partners/${this.PARTNER}/design-categories/${slug}/designs/`);
  }
  getProductCategories() {
    return this.getRequest(`partners/${this.PARTNER}/product-categories/`);
  }
  getProducts(slug) {
    return this.getRequest(`partners/${this.PARTNER}/product-categories/${slug}/products/`);
  }
  getFeaturedProducts() {
    return this.getRequest(`partners/${this.PARTNER}/featured/`);
  }
  getOrders() {
    return this.getRequest(`apps/${this.APP}/orders/`);
  }
  postOrder(data) {
    return this.postRequest(`apps/${this.APP}/orders/`, data);
  }
  getOrder(uuid) {
    return this.getRequest(`apps/${this.APP}/orders/${uuid}/`);
  }


  getRequest(path, params) {
    if (path.slice(-1) !== '/' && path.indexOf('?') === -1) {
      path = path + '/';
    }
    return new Promise((resolve, reject) => {
      this.authAxios.get(path, { params: params })
        .then((response) => resolve(response))
        .catch(error => {
        if (error.response && error.response.status && error.response.status === 401 || error.response.status === 403) {
          this.refresh()
            .then(() => this.authAxios.get(path, { params: params }))
            .then((response => resolve(response)))
            .catch((error) => {
              if (error.response && error.response.status && error.response.status === 401 || error.response.status === 403) {
                this.auth()
                  .then(() => this.authAxios.get(path, { params: params }))
                  .then((response => resolve(response)))
                  .catch(error => reject(error))
              } else {
                reject(error);
              }
            });
        } else {
          reject(error);
        }
      });
    });
  }
  postRequest(path, data) {
    if (path.slice(-1) !== '/') {
      path = path + '/';
    }
    return new Promise((resolve, reject) => {
      this.authAxios.post(path, data)
        .then((response) => resolve(response))
        .catch(error => {
          if (error.response && error.response.status && error.response.status === 401 || error.response.status === 403) {
            this.refresh()
              .then(() => this.authAxios.post(path, data))
              .then((response => resolve(response)))
              .catch((error) => {
                if (error.response && error.response.status && error.response.status === 401 || error.response.status === 403) {
                  this.auth()
                    .then(() => this.authAxios.post(path, data))
                    .then((response => resolve(response)))
                    .catch(error => reject(error))
                } else {
                  reject(error);
                }
              });
          } else {
            reject(error);
          }
        });
    });
  }
  putRequest(path, data) {
    if (path.slice(-1) !== '/') {
      path = path + '/';
    }
    return this.authAxios.put(path, data);
  }
  deleteRequest(path) {
    if (path.slice(-1) !== '/') {
      path = path + '/';
    }
    return this.authAxios.delete(path);
  }

  setAuthAxios(accessToken) {
    this.authAxios = axios.create({
      baseURL: this.API_URL,
      headers: {
        Authorization: 'Bearer ' + accessToken,
        'Content-Type': 'application/json',
        Accept: 'application/json'
      }
    });
  }
}

export default Backend;
