<template>
  <div class="dialog-for-who-component">
    <for-who
      v-if="currentStep === 'forWho'"
      @changeStep="onChangeStep"
    />
    <UniqueNameDialog
      v-if="currentStep === 'uniqueNameDialog'"
      @changeStep="onChangeStep"
    />
  </div>
</template>

<script>
import ForWho from './ForWho';
import ChooseDesignCategory from '../DialogDesign/ChooseDesignCategory';
import ChooseDesignSubcategory from '../DialogDesign/ChooseDesignSubcategory';
import UniqueNameDialog from './UniqueNameDialog';
import Backend from '../../services/backend';

const backend = new Backend();

export default {
  name: 'DialogForWho',
  props: ['model'],
  components: {
    ForWho,
    ChooseDesignCategory,
    ChooseDesignSubcategory,
    UniqueNameDialog
  },
  data() {
    return {
      currentStep: 'forWho'
    };
  },
  created() {
  },
  methods: {
    onChangeStep(step) {
      this.currentStep = step;
      if (this.currentStep === 'end') {
        this.$emit('closeDialog', { openNext: true });
        setTimeout(() => this.currentStep = 'forWho', 1000);
      }
    },
  }
};
</script>

<style lang="scss" scoped>
  .dialog-for-who-component {
    .card {
      min-height: 90vh;
      position: relative;
    }
    @media (max-width: 576px) {
      /*width: 95vw;*/
    }
  }
</style>
