import Vue from 'vue';
import Router from 'vue-router';
import HomePage from './views/HomePage.vue';
import DesignsPage from './views/DesignsPage';
import ProductsPage from './views/ProductsPage';
import CustomizeProductPage from  './views/CustomizeProductPage';
import CartPage from './views/CartPage';
import FeaturedProductsPage from './views/FeaturedProductsPage';
import SpecialShippingPage from './views/SpecialShippingPage';
import OrdersPage from './views/OrdersPage';

Vue.use(Router);

export default new Router({
  //mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: HomePage,
    },
    {
      path: '/cart',
      name: 'cart',
      component: CartPage
    },
    {
      path: '/orders',
      name: 'orders',
      component: OrdersPage
    },
    {
      path: '/cart/special_shipping',
      name: 'specialShipping',
      component: SpecialShippingPage
    },
    {
      path: '/:name/featured',
      name: 'featured',
      component: FeaturedProductsPage
    },
    {
      path: '/:name/:subcategory/',
      name: 'designs',
      component: DesignsPage
    },
    {
      path: '/:name/:subcategory/:design/',
      name: 'products',
      component: ProductsPage
    },
    {
      path: '/:name/:subcategory/:design/:productCategory',
      name: 'productCategory',
      component: ProductsPage
    },
    {
      path: '/:name/:subcategory/:design/:productCategory/:product',
      name: 'product',
      component: CustomizeProductPage
    },
  ],
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
});
