<template>
  <div class="quantity-input-component">
    <v-btn class="count-button blue-grey lighten-2" @click="decreaseCount">-</v-btn>
    <v-text-field
      :value="value"
      @input="setCount"
      type="number"
      class="count-to-order"
      name="count"
      min="1"
      flat
      solo
      hide-details
    ></v-text-field>
    <v-btn class="count-button blue-grey lighten-2" @click="increaseCount">+</v-btn>
  </div>
</template>

<script>
export default {
  name: 'QuantityInput',
  props: [ 'value' ],
  methods: {
    increaseCount() {
      this.setCount(this.value + 1);
    },
    decreaseCount() {
      this.setCount(this.value - 1);
    },
    setCount(count) {
      count = parseInt(count);
      if (isNaN(count) || count < 1) {
        count = 1;
      }
      this.$emit('input', count);
    }
  }
}
</script>

<style lang="scss">
  .quantity-input-component {
    .count-button {
      display: inline-block;
      width: 40px;
      min-width: 20px;
      padding: 0;
      color: #ffffff;
    }
    .count-to-order {
      width: 50px;
      outline: 1px solid #ccc;
      display: inline-block;
      .v-input__slot {
        padding: 0;
      }
      input {
        text-align: center;
      }
    }
    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
      /* display: none; <- Crashes Chrome on hover */
      -webkit-appearance: none;
      margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
    }
  }
</style>
