<template>
  <div class="nameinput-component">
    <v-form>
      <div class="wrapper">
        <v-combobox
          class="autocomplete"
          v-model="model"
          :items="namesList"
          :append-icon="null"
          item-text="name"
          item-value="slug"
          placeholder="Type your name here"
          @change="submit"
          :counter="0"
          hide-details
          attach
          flat
          solo
          :disabled="isLoading"
        >
        </v-combobox>
        <v-btn
          class="submit-button"
          :disabled="isLoading"
          :round="false"
          @click="submit"
          flat>
            <preloader v-if="isLoading" size="20" ></preloader>
            <v-icon v-else>search</v-icon>
        </v-btn>
      </div>
    </v-form>
  </div>
</template>

<script>
import Preloader from './Layout/Preloader';

export default {
  name: 'NameInput',
  components: { Preloader },
  data() {
    return {
      model: ''
    };
  },
  computed: {
    namesList() {
      return this.$store.state.namesList.list;
    },
    isLoading() {
      return this.$store.state.namesList.isLoading;
    }
  },
  methods: {
    submit() {
      setTimeout(() => {
        if (this.model) {
          this.$emit('submit', this.model);
          this.$store.commit('setName', this.model);
        }
      }, 200);
    },
  },
};
</script>

<style scoped lang="scss">
  .v-list__tile__content {
    color: red;
  }
</style>
<style lang="scss">
  .nameinput-component {
    .autocomplete {
      border-right: 1px solid rgba(0,0,0, .25);
    }
    .wrapper {
      outline: 1px solid rgba(0,0,0, .25);
      display: flex;
      width: 100%;
      max-width: 550px;
      margin-left: auto;
      margin-right: auto;
    }
    .submit-button {
      height: 48px;
      margin: 0;
      background: rgba(255,255,255,0.6);
    }
  }
</style>
