<template>
  <v-container class="for-who-component">
    <v-layout align-content-space-between column fill-height>
      <v-layout d-block>
        <h2 class="display-2 mb-3"> Wohoo {{ name }}!</h2>
        <h3 class="headline text-uppercase mb-3" >Who is this design for?</h3>
      </v-layout>
      <v-container grid-list-xl fluid>
        <v-layout justify-space-around wrap>
          <v-flex xs12 sm10 md6 d-block>
            <v-card
              class="card-variant"
              @click="selectForWho"
            >
              <v-icon class="card-icon" >person</v-icon>
              <h4 class="text-uppercase headline">Just me</h4>
            </v-card>

          </v-flex>
          <v-flex xs12 sm10 md6 d-block>
            <v-card
              class="card-variant"
              @click="selectForWho"
            >
              <v-icon class="card-icon">card_giftcard</v-icon>
              <h4 class="text-uppercase headline">Someone else</h4>
            </v-card>
          </v-flex>
        </v-layout>

      </v-container>

      <v-spacer></v-spacer>
    </v-layout>
  </v-container>
</template>

<script>
import '../../assets/scss/mixins.scss';
export default {
  name: 'ForWho',
  props: ['model'],
  data() {
    return {};
  },
  methods: {
    selectForWho() {
      if (this.$store.state.namesList.current.slug === null) {
        this.$emit('changeStep', 'uniqueNameDialog');
      } else {
        this.$emit('changeStep', 'end');
      }
    },
  },
  computed: {
    name() {
      return this.$store.state.namesList.current && this.$store.state.namesList.current.name;
    },
  },
};
</script>

<style lang="scss">
  @import "../../assets/scss/mixins";
  .for-who-component {
    .card-variant {
      cursor: pointer;
      background: #0795e7;
      color: #ffffff;
      padding-top: 25px;
      height: 300px;
      &:hover {
        background: #002453;
      }
      @include max-width(xs) {
        height: 200px;
      }
    }
    .card-icon {
      color: #ffffff;
      font-size: 200px;
      @include max-width(xs) {
        font-size: 100px;
      }
    }
  }
</style>
