<template>
  <v-container grid-list-xl class="special-shipping-component">
    <v-stepper value="2" alt-labels>
      <v-stepper-header>
        <v-stepper-step step="1" complete class="text-uppercase">Summary</v-stepper-step>
        <v-divider></v-divider>
        <v-stepper-step step="2" class="text-uppercase">Shipping</v-stepper-step>
      </v-stepper-header>
    </v-stepper>

    <v-layout row wrap>
      <v-flex xs12 md8>
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-layout row wrap>
            <h3 class="headline text-uppercase">Shipping information</h3>
            <v-flex xs12>
              <v-text-field
                v-model="form.fullName"
                :rules="rules.fullNameRules"
                label="Full Name"
              ></v-text-field>
            </v-flex>

            <v-flex xs12 sm6>
              <v-text-field
                v-model="form.addressLine1"
                :rules="rules.addressLine1Rules"
                label="Address Line 1"
                required
              ></v-text-field>
            </v-flex>
            <v-flex xs12 sm6>
              <v-text-field
                v-model="form.addressLine2"
                label="Address Line 2"
              ></v-text-field>
            </v-flex>

            <v-flex xs12 sm6>
              <v-text-field
                v-model="form.city"
                :rules="rules.cityRules"
                label="City"
              ></v-text-field>
            </v-flex>
            <v-flex xs12 sm6>
              <v-text-field
                v-model="form.state"
                label="State"
              ></v-text-field>
            </v-flex>

            <v-flex xs12 sm6>
              <v-text-field
                v-model="form.postalCode"
                label="Postal Code"
              ></v-text-field>
            </v-flex>
            <v-flex xs12 sm6>
              <v-text-field
                v-model="form.country"
                label="Country"
              ></v-text-field>
            </v-flex>

            <v-flex xs12 sm6>
              <v-text-field
                v-model="form.phoneNumber"
                :rules="rules.phoneNumberRules"
                label="Phone"
              ></v-text-field>
            </v-flex>
            <v-flex xs12 sm6>
              <v-text-field
                v-model="form.email"
                :rules="rules.emailRules"
                label="Email"
              ></v-text-field>
            </v-flex>

            <h3 class="headline text-uppercase">Shipping Method</h3>
            <v-flex xs12>
              <v-text-field
                v-model="form.shippingMethod"
                label="Please fill your address to see shipping options"
              ></v-text-field>
            </v-flex>

            <h3 class="headline text-uppercase">Coupon</h3>
            <v-flex xs12>
              <v-text-field
                v-model="form.coupon"
                label="Coupon Code"
              ></v-text-field>
            </v-flex>
          </v-layout>

        </v-form>
      </v-flex>

      <v-flex xs12 md4>
        <v-carousel light>
          <v-carousel-item
            v-for="(item,i) in cartItems"
            :key="i"
          >
            <v-layout align-center justify-center fill-height column>
              <ProductWithDesign
                :name="item.name"
                :product="item.productColor"
                :design="item.design"
                class="product-img"
              />
              <p class="subheading">{{ item.product.name }}</p>
            </v-layout>
          </v-carousel-item>
        </v-carousel>
      </v-flex>

      <v-flex class="pl-0">
        <v-btn color="info" @click="submit">Submit</v-btn>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import ProductWithDesign from '../components/ProductWithDesign';
import Backend from '../services/backend';

let backend = new Backend();
const formRules = {
  //commented unused rules
  //fullNameRules: [ v => !!v || 'Full Name is required' ],
  addressLine1Rules: [ v => !!v || 'Address Line 1 is required'],
  // addressLine2Rules: [],
  //cityRules: [ v => !!v || 'City is required' ],
  // stateRules: [ ],
  // postalCodeRules: [ v => !!v || 'Postal code is required' ],
  // countryRules: [],
  //phoneNumberRules: [ v => !!v || 'Phone is required' ],
  //emailRules: [ v => !!v || 'Email is required' ],
  // shippingMethodRules: [],
  // couponRules: []
};

export default {
  name: 'SpecialShipping',
  components: { ProductWithDesign },
  data() {
    return {
      valid: false,
      form: {
        fullName: '',
        addressLine1: '',
        addressLine2: '',
        city: '',
        state: '',
        postalCode: '',
        country: '',
        phoneNumber: '',
        email: '',
        shippingMethod: '',
        coupon: '',
      },
      rules: formRules
    };
  },
  computed: {
    cartItems() {
      return this.$store.state.cart.items;
    }
  },
  methods: {
    submit() {
      if (!this.$refs.form.validate()) {
      return;
      }
      let cartItems = this.$store.state.cart.items;
      let orderToSend = {
        address: this.form.addressLine1,
        items: cartItems.map((cartItem) => ({
          design: cartItem.design.slug,
          name: cartItem.name,
          product: cartItem.product.slug,
          product_color: cartItem.productColor.name,
          product_size: cartItem.productSize.name
        }))
      };
      backend.postOrder({packages: [orderToSend]}).then(this.onSuccess)
    },
    onSuccess(res) {
      this.$store.dispatch('clearCart');
      this.$router.push({ name: 'orders'});
    }
  }
}
</script>

<style lang="scss">
.special-shipping-component {
  text-align: left;
  .v-carousel__controls {
    background: #ffffff;
  }
}
</style>
