<template>
  <v-container grid-list-xl class="customize-product-component">
    <preloader v-if="isLoading"></preloader>
    <v-layout v-else row wrap justify-center>
      <v-flex xs11 md6 >
        <v-layout row align-center fill-height justify-center>
          <v-layout column class="sizes-block" align-center>
            <v-card
              class="size-item mb-2"
              v-for="item in product.sizes"
              :class="{'disabled': sizeIsDisabled(item), 'active': item.name === activeSize.name}"
              color="#ffffff"
              @click="selectActiveSize(item)"
            >
              <span class="size-text">{{ item.name }}</span>
            </v-card>
          </v-layout>
          <v-card v-if="currentColor.images" flat class="product-block">
            <ProductWithDesign
              :product="currentColor"
              :design="currentDesign"
              :name="$route.params.name"
            />
          </v-card>
          <v-layout column align-center class="colors-block"
                    :style="{ maxWidth: getColorsBlockSize(), minWidth: getColorsBlockSize() }">
            <v-card
              class="color-item mb-1"
              v-for="item in product.colors"
              :key="item.slug"
              @click="selectCurrentColor(item)"
              :color="item.value"
              :class="{'active': item.value === currentColor.value}"
            >
            </v-card>
          </v-layout>
        </v-layout>
      </v-flex>

      <v-flex xs11 md6 v-if="currentColor && currentColor.sizes && product">
        <h3 class="product-name headline mb-3">{{ product.name }}</h3>
        <div class="description subheading">
          <p>
            {{ product.description }}
          </p>
          <p>
            Price: $ {{ currentPrice }}
          </p>
          <p>
            <QuantityInput v-model="count"/>
          </p>
          <p>
            <v-btn color="info" @click="addToCart">Add to Cart</v-btn>
          </p>
        </div>
      </v-flex>
    </v-layout>
    <v-dialog v-model="addedToCartDialogIsOpen" max-width="450">
      <AddedToCartDialog :productName="product.name" @closeDialog="addedToCartDialogIsOpen=false"/>
    </v-dialog>
  </v-container>
</template>

<script>
import Preloader from '../components/Layout/Preloader';
import ProductWithDesign from '../components/ProductWithDesign';
import AddedToCartDialog from '../components/AddedToCartDialog';
import QuantityInput from '../components/QuantityInput';
import ModalDialog from '../components/Layout/ModalDialog';
import DialogDesign from '../components/DialogDesign/DialogDesign';

export default {
  name: 'CustomizeProductPage',
  components: {
    Preloader,
    ProductWithDesign,
    AddedToCartDialog,
    QuantityInput,
    ModalDialog,
    DialogDesign
  },
  watch: {
    '$route.params': 'loadData'
  },
  data() {
    return {
      currentColor: {},
      activeSize: {},
      count: 1,
      addedToCartDialogIsOpen: false
    }
  },
  created() {
    this.loadData();
  },
  computed: {
    product() {
      return this.$store.state.products.current || {};
    },
    currentColorUrl() {
      return this.currentColor && this.currentColor.images && this.currentColor.images[0].url;
    },
    isLoading() {
      return this.$store.state.products.isLoading;
    },
    currentDesign() {
      return this.$store.state.designs.current;
    },
    currentPrice() {
      let currentSizeObjArr = this.currentColor.sizes.find(item => {
        return item.name === this.activeSize.name;
      });
      return currentSizeObjArr && currentSizeObjArr.price;
    }
  },
  methods: {
    loadData() {
      this.$store.dispatch('fetchIfEmpty', { key: 'namesList' }).then(() => {
        if (!this.$store.state.namesList.current) {
          this.$store.commit('setNameSlug', this.$route.params.name);
        }
      });
      let productCategory = this.$route.params.productCategory || 'adult';
      this.$store.dispatch('fetchIfEmpty', { key: 'products', slug: productCategory }).then(() => {
        this.$store.commit('setCurrentProduct', this.$route.params.product);
        this.currentColor = this.product.colors[0];
        this.refreshCurrentSize();
      });
      this.$store.dispatch('fetchIfEmpty', { key: 'designs', slug: this.$route.params.subcategory }).then(() => {
        this.$store.commit('setCurrentDesign', this.$route.params.design);
      });
    },
    addToCart() {
      this.$store.dispatch('addCartItem', {
        name: this.$route.params.name,
        design: this.currentDesign,
        count: this.count,
        product: this.product,
        productColor: this.currentColor,
        productSize: this.activeSize,
      });

      this.addedToCartDialogIsOpen = true;
    },
    sizeIsDisabled(size) {
      if (!this.currentColor.sizes) return;
      return !this.currentColor.sizes.find(currentSize => {
        return size.name === currentSize.name;
      });
    },
    selectCurrentColor(color) {
      this.currentColor = color;
      this.refreshCurrentSize();
    },
    selectActiveSize(size) {
      if (this.sizeIsDisabled(size)) return;
      this.activeSize = size;
    },
    refreshCurrentSize() {
      this.activeSize = this.product.sizes.find(value => value.name === this.currentColor.sizes[0].name );
    },
    getColorsBlockSize() {
      if (this.product.colors && this.product.colors.length > 10) {
        return '30px';
      } else return '40px';
    }
  }
}
</script>

<style lang="scss">
.customize-product-component {
  .product-name {
    text-align: left;
  }
  .description {
    line-height: 1.4;
    text-align: left;
  }
  .sizes-block {
    max-width: 50px;
    min-width: 50px;
    margin-top: 0 !important;
    margin-bottom: 0 !important;
    margin-right: 5px !important;
    .size-text {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  .colors-block {
    margin-bottom: 0 !important;
    margin-top: 0 !important;
    margin-left: 5px !important;
  }
  .product-block {
  }
  .size-item,
  .color-item {
    cursor: pointer;
    width: 100%;
    padding-top: 100%;
    position: relative;
    &.active {
      outline: 3px solid #2196f3;
    }
  }
  .color-item {
  }
  .size-item {
    &.disabled {
      color: #dddddd;
      cursor: auto;
    }
  }
  .image-wrapper {
    position: relative;
    display: inline-block;
  }
  .image-logo {
    position: absolute;
  }
  .image-product {
    width: 100%;
  }

}
</style>
