<template>
  <v-container class="cart-page-component">
    <v-stepper value="1" alt-labels>
      <v-stepper-header>
        <v-stepper-step step="1" class="text-uppercase">Summary</v-stepper-step>
        <v-divider></v-divider>
        <v-stepper-step step="2" class="text-uppercase">Shipping</v-stepper-step>
      </v-stepper-header>
    </v-stepper>
    <h3 class="text-uppercase headline">Cart</h3>
    <v-data-table
      :headers="headers"
      :items="cartItems"
      class="mb-4"
      hide-actions
    >
      <template slot="headers" slot-scope="props">
        <th v-for="item in props.headers" class="title">
          {{ item.text }}
        </th>
      </template>
      <template slot="items" slot-scope="props">
        <td class="subheading" >
          <v-layout row align-center >
            <v-flex >
              <ProductWithDesign
                :name="props.item.name"
                :product="props.item.productColor"
                :design="props.item.design"
                class="product-img"
              />
            </v-flex>
          </v-layout>
        </td>
        <td>
          <v-layout column>
            <p class="title">{{ props.item.product.name }}</p>

            Size: {{ props.item.productSize.name }}
            Color: {{ props.item.productColor.name }}

          </v-layout>
        </td>
        <td class="text-xs-center subheading">${{ props.item.productColor.sizes[0].price }}</td>
        <td class="text-xs-center subheading">
          <div class="quantity-wrapper">
            <QuantityInput :value="props.item.count" @input="setItemCount(props.item, $event)"/>
          </div>
        </td>
        <td class="text-xs-center subheading">
          ${{ (parseFloat(props.item.productColor.sizes[0].price)* props.item.count).toFixed(2)  }}
        </td>
        <td>
          <v-btn @click="deleteCartItem(props.item)" class="error">Remove</v-btn>
        </td>
      </template>
      <template slot="no-data">
        <v-alert :value="true" color="red lighten-2" icon="warning">
          Cart is empty
        </v-alert>
      </template>
    </v-data-table>
    <div class="text-xs-left">
      <v-btn color="info" v-if="cartItems && cartItems.length" :to="{ name: 'specialShipping' }">Shipping</v-btn>
    </div>
  </v-container>
</template>

<script>
import Backend from '../services/backend';
import ProductWithDesign from "../components/ProductWithDesign";
import QuantityInput from '../components/QuantityInput';

const backend = new Backend();
const headers = [
  { text: '' },
  { text: 'Item' },
  { text: 'Price' },
  { text: 'Quantity' } ,
  { text: 'Total' },
  { text: '' }
];

export default {
  name: 'CartPage',
  components: { ProductWithDesign, QuantityInput },
  data() {
    return {
      headers: headers
    };
  },
  created() {
  },
  computed: {
    cartItems() {
      return this.$store.state.cart.items;
    }
  },
  methods: {
    deleteCartItem(item) {
      this.$store.dispatch('deleteCartItem', item);
    },
    setItemCount(item, count) {
      this.$store.dispatch('setCartItemCount', { item, count });
    }
  }
}
</script>

<style lang="scss">
.cart-page-component {
  .product-img {
    max-width: 100px;
    min-width: 50px;
  }
  .quantity-wrapper {
    display: inline-block;
    white-space: nowrap;
  }
}
</style>
