<template>
  <v-container class="choose-design-category-component choose-design-component">
    <v-layout align-content-space-between column>
      <v-flex>
        <v-layout row fill-height>
          <v-flex xs12>
            <h3 class="display-2 mb-4">Choose a Category</h3>
          </v-flex>
        </v-layout>
        <v-container grid-list-xl>
          <Preloader v-if="isLoading" />
          <v-layout v-else :justify-center="$vuetify.breakpoint.xs" :justify-left="$vuetify.breakpoint.smAndUp" wrap>

          <!--featured items link-->
          <v-flex xs10 sm4 md3 l3 d-block class="featured-item">
            <v-card v-if="name" color="grey lighten-3" class="category-card" :to="{name: 'featured', params: { name: name }}">
              <v-img class="featured-image">
                <v-layout row fill-height align-center>
                  <v-flex>
                    <div>
                      <v-icon size="50" color="#673AB7">star</v-icon>
                    </div>
                    <span class="featured-text text-uppercase headline text-center font-weight-bold">Featured<br>items</span>
                  </v-flex>
                </v-layout>
              </v-img>
            </v-card>
          </v-flex>

          <v-flex xs10 sm4 md3 l3 d-block
            v-for="item in designCategoriesList"
            :key="item.slug"
          >
            <v-card
              color="grey lighten-3"
              class="category-card"
            >
              <v-img
                :src="item.image"
                @click="selectCategory(item)"
                aspect-ratio="1"
              >
                <v-layout row fill-height align-center>
                  <v-flex>
                    <span class="card-text text-uppercase headline text-center font-weight-bold">{{ item.name }}</span>
                  </v-flex>
                </v-layout>
              </v-img>
            </v-card>
          </v-flex>
        </v-layout>
        </v-container>
      </v-flex>

      <v-spacer></v-spacer>
    </v-layout>
  </v-container>
</template>

<script>
import Preloader from "../Layout/Preloader";

export default {
  name: 'ChooseDesignCategory',
  components: { Preloader },
  data() {
    return {};
  },
  methods: {
    selectCategory(item) {
      this.$store.commit('setDesignCategory', item);
      this.$emit('submit', 'chooseDesignSubcategory');
    }
  },
  computed: {
    name() {
      return this.$store.state.namesList.current &&
        (this.$store.state.namesList.current.slug || this.$store.state.namesList.current.name);
    },
    designCategoriesList() {
      return this.$store.state.designCategories.list || [];
    },
    isLoading() {
      return this.$store.state.designCategories.isLoading;
    }
  },
};
</script>

<style lang="scss">
  // common component for design category and subcategory
  .choose-design-component {
    .category-card {
      cursor: pointer;
      .v-responsive__content>.layout {
        margin-top: 0 !important;
        margin-bottom: 0 !important;
      }
      .v-image__image {
        filter: brightness(75%);
        transition: filter 0.3s linear;
      }
      &:hover {
        .v-image__image {
          filter: brightness(100%);
        }
      }
    }
    .card-text {
      color: #ffffff;
      text-shadow: 0 0 2px #000;
    }
  }
  .choose-design-category-component {
    .featured-item {
      position: relative;
      &>a {
        position: relative;
        height: 100%;
      }
    }
    .featured-image {
      height: 100%;
      background: #E3F2FD;
    }
    .featured-text {
      color: #673AB7;
    }
  }
</style>
