<template>
  <v-container class="orders-page-component">
    <h3 class="text-uppercase headline text-xs-center mb-4">Orders</h3>
    <v-layout v-if="isLoading" justify-center row class="mt-4">
      <Preloader size="80"/>
    </v-layout>
    <v-card v-else v-for="order in orders" class="order-item" >
      <v-layout row class="subheading mb-2">
        <v-flex xs5>
          <p class="font-weight-bold">Order status: </p>
        </v-flex>
        <p >{{order.status}}</p>

      </v-layout>
      <div v-for="pack in order.packages" class="mb-4">
        <v-layout row class="subheading">
          <v-flex xs5>
            <p class="font-weight-bold">Address: </p>
          </v-flex>
            <p>{{pack.address}}</p>
        </v-layout>
        <v-layout row class="subheading">
          <v-flex xs5>
            <p class="font-weight-bold">Shipping price: </p>
          </v-flex>
            <p>$ {{pack.shipping_price}}</p>
        </v-layout>
        <v-data-table
          :headers="headers"
          :items="pack.items"
          hide-actions
          :class="{ 'mx-5' : $vuetify.breakpoint.smAndUp }"
        >
          <template slot="headers" slot-scope="props">
            <th v-for="item in props.headers" class="text-xs-center">
              {{ item.text }}
            </th>
          </template>
          <template slot="items" slot-scope="props">
            <td class="text-xs-center" >
              {{props.item.name}}
            </td>
            <td class="text-xs-center ">
              {{props.item.design}}
            </td>
            <td class="text-xs-center ">
              {{props.item.product}}
            </td>
            <td class="text-xs-center ">
              {{props.item.price}}
            </td>
          </template>
        </v-data-table>
      </div>
    </v-card>
  </v-container>
</template>

<script>
import Backend from '../services/backend';
import ProductWithDesign from "../components/ProductWithDesign";
import QuantityInput from '../components/QuantityInput';
import Preloader from '../components/Layout/Preloader';

const backend = new Backend();
const headers = [
  { text: 'Name' },
  { text: 'Design' },
  { text: 'Product' },
  { text: 'Price' }
];

export default {
  name: 'OrdersPage',
  components: { ProductWithDesign, QuantityInput, Preloader },
  data() {
    return {
      headers: headers,
    };
  },
  created() {
    this.$store.dispatch('fetchIfEmpty', { key: 'orders' });
  },
  computed: {
    isLoading() {
      return this.$store.state.orders.isLoading;
    },
    orders() {
      return this.$store.state.orders.list;
    }
  },
  methods: {
    deleteCartItem(item) {
      this.$store.dispatch('deleteCartItem', item);
    },
    setItemCount(item, count) {
      this.$store.dispatch('setCartItemCount', { item, count });
    }
  }
}
</script>

<style lang="scss">
.orders-page-component {
  text-align: left;

  .order-item {
    border: 1px solid rgba(0, 0, 0, 0.12);
    margin-bottom: 60px;
    padding: 40px 20px 20px;
    border-radius: 5px;
    /*background-color: rgba(10, 50, 10, 0.01);*/
  }
}
</style>
